.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: auto;
  max-width: 100%;
  min-width: 240px;
  padding: 40px;
  padding-bottom: 10px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center; /* Ensure the title stays centered */
}

.App-title {
  font-size: 1.75em;
  text-align: center;
  padding-left: .5%;
  
}

.App-intro {
  font-size: large;
}


.user-image {
  position: absolute; /* Position relative to the viewport */
  top: .25%; /* Adjust vertical alignment */
  left: 7%; /* Adjust horizontal alignment */
  width: 15vw;
  max-width: 160px;
  min-width: 100px;
  height: auto; /* Match size */
  border-radius: 50%; /* Optional: Circular image */
  -webkit-box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* Optional: Add a shadow */
  z-index: 10; /* Ensure it appears above other elements */
}


.ai-Tic-Tac-Toe {
  max-Width: "100px";
  border-Radius: "50%";
  margin-Bottom: "10px";
  height: 100px;

}




@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}




@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



